<template>
  <div>
<nav-bar :name="title"></nav-bar>
        <v-container class="text-center">
          <router-link to="/adduser">
            <v-btn color="#E47911" large height="40px" dark>
              <v-icon
                left
                dark
                > mdi-plus 
              </v-icon>  Add a new user
            </v-btn>
          </router-link>
        </v-container>

        <v-container class="text-center">
        <v-card class="ma-auto" width="90%" outlined>
          <br>
          <v-card class="ma-auto" width="75%" tile>
            <p class="font-weight-bold text-left px-3 py-1">Types of users</p>
            <v-divider></v-divider>
              <v-expansion-panels focusable>
                <v-expansion-panel
                  v-for="type in types"
                  :key="type.name"
                >
                  <v-expansion-panel-header class="font-weight-light text-left px-2">{{ type.name }}</v-expansion-panel-header>
                  <v-expansion-panel-content>
                    <v-card>
                      <v-layout>
                    <v-col class="text-right">
                      <v-btn
                        class="mx-auto"
                        icon
                        right
                        @click="activeExport"
                      >
                        <v-icon size="24px" :color="export_btn_color">
                          mdi-file-move
                        </v-icon>
                      </v-btn>
                    </v-col>
                      </v-layout>
                    <v-card-title>
                      <v-text-field
                        v-model="search"
                        append-icon="mdi-magnify"
                        label="Search"
                        single-line
                        hide-details
                      ></v-text-field>
                    </v-card-title>
                    <v-data-table
                      :headers="headers"
                      :search="search"
                      :items="type.value"
                      :loading="loading"
                      loading-text="Loading... Please wait"
                      hide-default-footer
                      :page.sync="page"
                      :items-per-page="itemsPerPage"
                      @page-count="pageCount = $event"
                      >
                      <template v-slot:item.first_name="{ item }">
                        {{item.first_name +' '+ item.last_name}}
                      </template>
                      <template v-slot:item.unique_id="{ item }">
                        <v-menu
                          top
                          offset-x
                          v-if="select"
                        >
                          <template v-slot:activator="{ on, attrs }">
                          <v-btn icon
                              dark
                              v-bind="attrs"
                              v-on="on"
                          >
                              <v-icon right color="#E47911" size="26px" class="mb-2"> mdi-arrow-down-drop-circle </v-icon>
                          </v-btn>
                          </template>
                          <v-list>
                          <v-list-item  link>
                            <v-list-item-title> Show user profile </v-list-item-title>
                          </v-list-item>
                          <v-list-item  link>
                            <v-list-item-title>
                              <router-link :to="{ name: 'UpdateUser', params: { 'id': item.unique_id }}"> Update user profile </router-link>
                            </v-list-item-title>
                          </v-list-item>
                          <v-list-item  link>
                            <v-list-item-title @click="delete_user(item.unique_id)"> Delete register </v-list-item-title>
                          </v-list-item>
                          </v-list>
                        </v-menu>
                          <v-checkbox
                              :value="item.unique_id"
                              color="#E47911"
                              v-model="idUser"
                              v-else
                              class="my-n3"
                          ></v-checkbox>
                      </template>
                    </v-data-table>
                    <v-pagination
                      v-model="page"
                      :length="pageCount"
                    ></v-pagination>
                  </v-card>
                <v-row v-if="!select">
                  <v-col>
                  <v-btn
                  class="mx-4 my-2"
                  color="#989898"
                  dark
                  @click="desactivarBtn"
                  >
                  CANCEL
                  </v-btn>
                  <v-btn
                  class="mx-4 my-2"
                  color="#E47911"
                  dark
                  @click="export_users"
                  >
                  EXPORT
                  </v-btn>
                  </v-col>
                </v-row>
                  </v-expansion-panel-content>
                </v-expansion-panel>
              </v-expansion-panels>
          </v-card>
          <br>
        </v-card>
        </v-container>
<footer-nav></footer-nav>
  </div>
</template>

<script>
  import NavBar from '@/components/NavBar';
  import FooterNav from '@/components/Footer';
  import UserService from '@/services/UserService.js';
  import Swal from 'sweetalert2'

  export default {
    components: {
      NavBar, FooterNav
    },
    data: () => ({
      title: 'USERS',
      select: true,
      export_btn_color: '#000',
      loading: true,
      page: 1,
      pageCount: 0,
      itemsPerPage: 5,
      search: '',
      users_admin: [

      ],
      users_clients: [

      ],
      idUser: [],
      headers: [
        {
          text: 'Name',
          value: 'first_name'
        },
        { 
          text: 'Actions',
          value: 'unique_id',
          sortable: false,
          search: false
        },
      ],
      types: [
        {
          name: "Admins",
          value: []
        },
        {
          name: "Clients",
          value: []
        },
      ],
    }),

    mounted() {
      UserService
        .list_admin()
        .then(res => {
          console.log(res)
          this.types[0].value = res.filter(item => item.is_staff);
          
          this.types[1].value = res.filter(item => !item.is_staff);

          this.loading = false
        })
        .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
    },

    methods: {
      activeExport(){
        this.select = !this.select;
        this.export_btn_color = !this.select ? '#E47911' : '#000'
      },
      
      desactivarBtn(){
        this.select = !this.select;
        this.export_btn_color = '#000'
      },

      refresh_list(){
        this.loading = true
        UserService
          .list_admin()
          .then(res => {
            this.types[0].value = res.filter(item => item.is_staff);
            this.types[1].value = res.filter(item => !item.is_staff);
            this.loading = false
          })
          .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
      },

      delete_user(pk){
        Swal.fire({
          icon: 'question',
          title: 'Confirm',
          text: 'Are you sure you want to delete this user?',
          confirmButtonText: 'YES',
          cancelButtonText: 'NO',
          showCancelButton: true
        }).then(res => {
          if(res.isConfirmed){
            UserService
            .delete(pk)
            Swal.fire({
              icon: 'success',
              title: 'Success',
              text: 'User successfully removed.'
            }).then(() => {
                this.refresh_list();
              })
          }
        })
        .catch(e => {
          let error = e.response.data;
          let msg = ''
          Object.entries(error).forEach(([ key, value ]) => {
            msg+= key + ': ' + value + ' \n ';
          });
            Swal.fire({
            icon: 'error',
            title: 'Error',
            text: msg
            });
          })
      },

      export_users(){
        UserService
          .export_users(this.idUser)
          .then(res => {
              const url = window.URL.createObjectURL(new Blob([res.data]));
              const link = document.createElement('a');
              link.href = url;
              link.setAttribute('download', 'users.xlsx'); //or any other extension
              document.body.appendChild(link);
              link.click();
          })
          .catch(e => {
            let error = e.response.data;
            let msg = ''
            Object.entries(error).forEach(([ key, value ]) => {
              msg+= key + ': ' + value + ' \n ';
            });
              Swal.fire({
              icon: 'error',
              title: 'Error',
              text: msg
              });
          })
      },
  },
  }
</script>